/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { useState, useEffect, useCallback, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import io from 'socket.io-client';
import { point, distance } from '@turf/turf';
import { toast } from 'react-toastify';
import DateRangePicker from 'react-daterange-picker';
import 'react-daterange-picker/dist/css/react-calendar.css';
import moment from 'moment';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { Table } from '../../../components';
import ViewOrderModal from './ViewOrderModal';
import TrackOrderModal from './TrackOrderModal';
import {
  getAllChefOrderSaga,
  getOrderCountSaga,
  resetOrdersListChef,
} from '../../../store/actions';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import SelectVehicleType from './SelectVehicleType';

const SOCKET_URI = 'http://3.21.49.79:6025';

// eslint-disable-next-line no-unused-vars
const Orders = props => {
  const [modelOpen, setModelOpen] = useState(false);
  const [modelOpenTrack, setModelOpenTrack] = useState(false);
  const [modelOpenSearchCard, setModelOpenSearchCard] = useState(false);
  const [mounted, setMounted] = useState(false);
  const [selectedData, setSelectedData] = useState(null);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const [isToggledDatePicker, setIsToggled] = useState(false);
  const [dateRangeValue, setDateRangeValue] = useState(null);
  const [tableFilter, setTableFilter] = useState({});
  const [filterSearch, setFilterSearch] = useState({
    isSelfPickUp: '',
    status: '',
    startDate: '',
    endDate: '',
  });
  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };
  const toggleDatePicker = useCallback(() =>
    setIsToggled(!isToggledDatePicker),
  );
  const {
    orders: { list: allData, totalRecords },
    orderCount: {
      acceptedOrderCount,
      deliveredOrderCount,
      preparingOrderCount,
      pendingOrderCount,
      pickedUpOrderCount,
      rejectedOrderCount,
      totalOrderCount,
    },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.order);
  const { open } = useSelector(state => state.modal);
  const childRef = useRef();
  const socket = useRef();

  const dispatch = useDispatch();
  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllChefOrderSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
          filterSearch,
        }),
      );
    } else {
      dispatch(
        getAllChefOrderSaga({
          columnName,
          orderBy,
          search,
          skip,
          limit,
          filterSearch,
        }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    dispatch(getOrderCountSaga());
    getData('createdAt', 'desc', '', 0, 10);
    return () => {
      dispatch(resetOrdersListChef());
      setMounted(false);
    };
  }, []);

  const searchCardRequestEvent = vehicleType => {
    if (selectedData.status !== 'ReadyForPickup') {
      return;
    }

    const socketToken = localStorage.getItem('socketToken');
    const userData = JSON.parse(localStorage.getItem('userData'));

    const pickup = {
      latitude: userData.chefDetails?.lat
        ? parseFloat(userData.chefDetails.lat)
        : '',
      longitude: userData.chefDetails?.long
        ? parseFloat(userData.chefDetails?.long)
        : '',
    };

    socket.current = io(SOCKET_URI, {
      // Temporary
      query: `token=${socketToken}`,
      transports: ['websocket'],
    });

    socket.current.on('connect', () => {
      console.log(socket.current.connected); // true

      const from = point([pickup.longitude, pickup.latitude]);
      const to = point([
        selectedData.orderDeliverAddress?.longitude,
        selectedData.orderDeliverAddress?.latitude,
      ]);

      const distanceInKm = distance(from, to);
      socket.current.emit(
        'search-car-on-request-ride',
        {
          lat: pickup.latitude,
          long: pickup.longitude,
          pickupAddress: JSON.stringify(selectedData.restaurantAddress),
          destinationAddress: JSON.stringify(selectedData.orderDeliverAddress),
          destinationLat: selectedData.orderDeliverAddress?.latitude,
          destinationLong: selectedData.orderDeliverAddress?.longitude,
          totalDistance: `${distanceInKm} km`,
          vehicleId: vehicleType,
          orderId: selectedData._id,
        },
        response => {
          console.log(response.status); // ok

          if (response.status === 200) {
            toast(response.message, {
              position: 'top-center',
              hideProgressBar: true,
              containerId: 'appLayoutToaster',
              type: 'success',
              autoClose: 3500,
            });
          } else {
            toast(response.message, {
              position: 'top-center',
              hideProgressBar: true,
              containerId: 'appLayoutToaster',
              type: 'error',
              autoClose: 3500,
            });
          }
        },
      );
    });
  };

  useEffect(() => {
    if (mounted) {
      if (
        filterSearch.status === '' &&
        filterSearch.isSelfPickUp === '' &&
        filterSearch.startDate === '' &&
        filterSearch.endDate === ''
      ) {
        getData('createdAt', 'desc', '', 0, 10);
      }
    }
  }, [filterSearch]);

  const viewOrder = data => {
    setSelectedData(data);
    setModelOpen(true);
  };

  const trackOrder = data => {
    setSelectedData(data);
    setModelOpenTrack(true);
  };

  const searchCard = data => {
    setSelectedData(data);
    setModelOpenSearchCard(true);
  }

  const tableFields = [
    {
      displayName: 'Order Number',
      key: 'orderNumber',
    },
    {
      displayName: 'Date Time',
      key: 'createdAt',
      isDateTime: true,
    },
    {
      displayName: 'Customer Name',
      key: 'userName',
    },
    {
      displayName: 'Customer Number',
      key: 'phone',
    },
    {
      displayName: 'Order Type',
      key: 'isSelfPickUp',
      isConditional: true,
      positiveValue: 'PickUp',
      negativeValue: 'Delivery',
    },
    // {
    //   displayName: 'Vendor Name',
    //   key: 'restaurantName',
    // },
    {
      displayName: 'Payment Mode',
      key: 'isCODOrder',
      isConditional: true,
      positiveValue: 'COD',
      negativeValue: 'Online',
    },
    {
      displayName: 'Order Status',
      key: 'status',
      isCapitalize: true,
    },
    {
      displayName: 'Order Amount',
      key: 'grandTotal',
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: viewOrder,
        },
        {
          view: <i className="fa fa-map-marker" aria-hidden="true" />,
          action: trackOrder,
          conditionalKey: 'status',
          conditionalValue: 'Rejected',
        },
        {
          view: ({ status, isSelfPickUp }) => {
            // status !== 'ReadyForPickup' ? { color: 'red' } : ''
            return (
              <i
                className={`fa fa-truck ${
                  status !== 'ReadyForPickup' || isSelfPickUp ? 'disable-click' : ''
                }`}
                aria-hidden="true"
              />
            );
          },
          action: searchCard,
        },
      ],
    },
  ];

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  const handleChangeFilter = (value, name) => {
    const filter = { ...filterSearch };
    filter[name] = value;
    setFilterSearch(filter);
  };

  const handleSelectDateRange = range => {
    setDateRangeValue(range);
    handleChangeFilter(range.start.format('yyyy-MM-DD'), 'startDate');
    handleChangeFilter(range.end.format('yyyy-MM-DD'), 'endDate');
    toggleDatePicker();
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Orders </h2>
              </div>
              <div className="row m-b-sm">
                <div className="col-xs-12 col-sm-6 col-lg-2">
                  <h4>Order type:</h4>
                  <select
                    className="form-control "
                    onChange={e =>
                      handleChangeFilter(e.target.value, 'isSelfPickUp')
                    }
                    value={filterSearch.isSelfPickUp}
                  >
                    <option value="">Select order Type</option>
                    <option value="false">Delivery</option>
                    <option value="true">Pick Up</option>
                  </select>
                </div>
                <div className="col-xs-12 col-sm-6 col-lg-2">
                  <h4>Order status:</h4>
                  <select
                    className="form-control"
                    onChange={e => handleChangeFilter(e.target.value, 'status')}
                    value={filterSearch.status}
                  >
                    <option value="">Select order status</option>
                    <option value="Pending">Pending</option>
                    <option value="Accepted">Accepted</option>
                    <option value="Rejected">Rejected</option>
                    <option value="Preparing">Preparing</option>
                    <option value="ReadyForPickup">Ready for pickup</option>
                    <option value="OnTheWayToPickUp">
                      On the way to pickup
                    </option>
                    <option value="PickedUp">PickedUp</option>
                    <option value="OnTheWayToDelivery">
                      On the way to delivery
                    </option>
                    <option value="Delivered">Delivered</option>
                    <option value="Canceled">Cancelled</option>
                  </select>
                </div>
                <div className="col-sm-12 col-lg-8">
                  <h4>Select Date:</h4>
                  <div className="row">
                    <div className="col-xs-12 col-sm-8 col-lg-8">
                      {isToggledDatePicker ? (
                        <>
                          {/* eslint-disable-next-line jsx-a11y/no-static-element-interactions */}
                          <div
                            className="modal-backdrop fade in date-backdrop"
                            onClick={() => {
                              toggleDatePicker();
                            }}
                          />
                          <DateRangePicker
                            initialRange={(moment(), moment().add(2, 'days'))}
                            // minimumDate={moment()}
                            value={dateRangeValue}
                            onSelect={handleSelectDateRange}
                          />
                        </>
                      ) : (
                        ''
                      )}
                      <div className="mb-10" id="data_5">
                        <div
                          className="input-daterange input-group"
                          id="datepicker"
                        >
                          <span className="input-group-addon">
                            <i className="fa fa-calendar" />
                          </span>
                          <input
                            type="text"
                            className="input-sm form-control"
                            name="start"
                            value={
                              (dateRangeValue &&
                                dateRangeValue.start != null &&
                                dateRangeValue.start.format('DD/MM/yyyy')) ||
                              ''
                            }
                            onClick={e => {
                              toggleDatePicker();
                              e.target.blur();
                            }}
                          />
                          <span className="input-group-addon bor-left">to</span>
                          <span className="input-group-addon">
                            <i className="fa fa-calendar" />
                          </span>
                          <input
                            type="text"
                            className="input-sm form-control"
                            name="end"
                            value={
                              (dateRangeValue &&
                                dateRangeValue.end != null &&
                                dateRangeValue.end.format('DD/MM/yyyy')) ||
                              ''
                            }
                            onClick={e => {
                              toggleDatePicker();
                              e.target.blur();
                            }}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-xs-6 col-sm-2 col-lg-2">
                      <button
                        className="btn btn-primary with-100"
                        type="submit"
                        onClick={() => {
                          getData('name', 'asc', '', 0, 10);
                          childRef.current.setCurrentPageChild();
                        }}
                      >
                        Apply
                      </button>
                    </div>
                    <div className="col-xs-6 col-sm-2 col-lg-2">
                      <button
                        className="btn btn-primary with-100"
                        type="submit"
                        style={{
                          background: '#80bbe7',
                          borderColor: '#80bbe7',
                        }}
                        onClick={() => {
                          setFilterSearch({
                            isSelfPickUp: '',
                            status: '',
                            startDate: '',
                            endDate: '',
                          });
                          setDateRangeValue({});
                          childRef.current.setCurrentPageChild();
                        }}
                      >
                        Reset
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div id="color">
                <div className="colours_action">
                  <ul className="animated zoomIn">
                    <li>
                      <div className="icons-circle-number orange">
                        {pendingOrderCount}
                      </div>
                      <h4>Pending</h4>
                    </li>
                    <li>
                      <div className="icons-circle-number dark-green">
                        {acceptedOrderCount}
                      </div>
                      <h4>Accepted</h4>
                    </li>
                    <li>
                      <div className="icons-circle-number violet">
                        {preparingOrderCount}
                      </div>
                      <h4>Preparing</h4>
                    </li>
                    <li>
                      <div className="icons-circle-number orange">
                        {deliveredOrderCount}
                      </div>
                      <h4>Delivered</h4>
                    </li>
                    <li>
                      <div className="icons-circle-number dark-green">
                        {rejectedOrderCount}
                      </div>
                      <h4>Rejected</h4>
                    </li>
                    <li>
                      <div className="icons-circle-number violet">
                        {pickedUpOrderCount}
                      </div>
                      <h4>Picked up</h4>
                    </li>
                    <li>
                      <div className="icons-circle-number green">
                        {totalOrderCount}
                      </div>
                      <h4>Total</h4>
                    </li>
                  </ul>
                </div>
              </div>
              <div className="coman-table" id="owner-detail-btn2">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  // link={linkObj}
                  linkPropsName="_id"
                  // statusAction={statusAction}
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  // pageSizeProp={pageSize}
                  ref={childRef}
                  initialSortColumnName="createdAt"
                  initialSortOrderBy="desc"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      {modelOpen && (
        <ViewOrderModal setModelOpen={setModelOpen} orderData={selectedData} />
      )}
      {modelOpenTrack && (
        <TrackOrderModal
          setModelOpen={setModelOpenTrack}
          orderData={selectedData}
          tableFilter={tableFilter}
        />
      )}
      {modelOpenSearchCard && (
        <SelectVehicleType
          setModelOpen={setModelOpenSearchCard}
          orderData={selectedData}
          searchCardRequestEvent={searchCardRequestEvent}
        />
      )}
      {open && <AlertMessageModal />}
    </div>
  );
};

export default SetTokenHeader(Orders, axios);
