import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Model from '../../../components/UI/Model/Model';

const SelectVehicleType = props => {
  const [mounted, setMounted] = useState(false);
  const [data, setData] = useState(null);
  const [vehicleType, setVehicleType] = useState('');
  const [isError, setIsError] = useState(false);

  const getData = async () => {
    try {
      const res = await axios.get('http://3.21.49.79:6025/V11/auth/getAllVehicleTypes');
      if(res.status === 200) {
        setData(res.data.data.vehicleType);
      } else {
        setData([]);
      }
    } catch(err) {
      setData([]);
    }
  }
  
  useEffect(() => {
    if(!mounted) {
      getData();
    }
    setMounted(true);
    return () => {
      setMounted(false);
    };
  }, []);

  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };

  const handleSubmit = () => {
    if(vehicleType !== '') {
      setIsError(false);
      const { searchCardRequestEvent } = props;
      searchCardRequestEvent(vehicleType);
      closeModel();
    } else {
      setIsError(true);
    }
  }

  return (
    <Model
      closeModel={closeModel}
      headerTitle="Vehicle Type"
      modalId="trackDeliveryBoy"
      modalClass="modal-sm"
      //   submitBtnText="Cancel"
    >
      {data !== null ? (
        <>
          <select className="form-control" onChange={(e) => setVehicleType(e.target.value)}>
            <option value="">Select Vehicle Type</option>
            {data.map(item => <option value={item._id}>{item.type.en}</option>)}
          </select>
          <div className="error-message">{isError && 'Please select vehicle type'}</div>
          <div className="col-12 text-center" style={{ marginTop: 10 }}>
            <button
              className="btn btn-primary"
              type="button"
              onClick={handleSubmit}
            >
              Submit
            </button>
          </div>
        </>
      ) : <div className="col-12 text-center">Loading...</div>}
    </Model>
  );
};

export default SelectVehicleType;
