import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';
import Model from '../../../components/UI/Model/Model';
import {
  getOrderTrackChefSaga,
  editOrderStatusSaga,
  resetOrderTrack,
} from '../../../store/actions';

const trackingOrderStatus = [
  'Pending',
  'Accepted',
  'Preparing',
  'ReadyForPickup',
  'OnTheWayToPickUp',
  'PickedUp',
  'OnTheWayToDelivery',
  'Delivered',
];

const TrackOrderModal = props => {
  const { orderData, tableFilter } = props;
  const [statusButtonText, setStatusButtonText] = useState('');
  const [time, setTime] = useState(45);
  const [isStatusAvailable, setIsStatusAvailable] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const {
    orderTrack,
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.order);
  const indexTrackingOrderStatus = trackingOrderStatus.indexOf(
    orderTrack.status,
  );

  const dispatch = useDispatch();

  useEffect(() => {
    setMounted(true);
    dispatch(getOrderTrackChefSaga({ id: orderData._id }));
    if (indexTrackingOrderStatus !== 7) {
      setTimeout(() => {
        setTime(time - 1);
      }, 1000);
    }
    return () => {
      setMounted(false);
      dispatch(resetOrderTrack());
    };
  }, []);

  useEffect(() => {
    if (time === 0) {
      setTime(45);
      dispatch(getOrderTrackChefSaga({ id: orderData._id }));
    } else {
      // eslint-disable-next-line no-lonely-if
      if (indexTrackingOrderStatus !== 7) {
        setTimeout(() => {
          setTime(time - 1);
        }, 1000);
      }
    }
  }, [time]);

  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };

  if (
    (orderTrack.status === 'Accepted' ||
      orderTrack.status === 'Preparing' ||
      orderTrack.status === 'ReadyForPickup' ||
      orderTrack.status === 'OnTheWayToPickUp') &&
    !isStatusAvailable
  ) {
    if (orderTrack.status === 'Accepted') {
      setStatusButtonText('Preparing');
      setIsStatusAvailable(true);
    }
    if (orderTrack.status === 'Preparing') {
      setStatusButtonText('Ready for Pickup');
      setIsStatusAvailable(true);
    }
    if (orderTrack.status === 'ReadyForPickup' && orderTrack.isSelfPickUp) {
      setStatusButtonText('Delivered');
      setIsStatusAvailable(true);
    }
    // if (orderTrack.status === 'OnTheWayToPickUp' && !orderTrack.isSelfPickUp) {
    //   setStatusButtonText('Picked up');
    //   setIsStatusAvailable(true);
    // }
  }

  const handleStatusChanges = () => {
    if (statusButtonText === 'Preparing') {
      dispatch(
        editOrderStatusSaga({
          status: 'Preparing',
          id: orderTrack._id,
          closeModel,
          tableFilter,
        }),
      );
    }
    if (statusButtonText === 'Ready for Pickup') {
      dispatch(
        editOrderStatusSaga({
          status: 'ReadyForPickup',
          id: orderTrack._id,
          closeModel,
          tableFilter,
        }),
      );
    }
    if (statusButtonText === 'Delivered') {
      dispatch(
        editOrderStatusSaga({
          status: 'Delivered',
          id: orderTrack._id,
          closeModel,
          tableFilter,
        }),
      );
    }
    // if (statusButtonText === 'Picked up') {
    //   dispatch(
    //     editOrderStatusSaga({
    //       status: 'PickedUp',
    //       id: orderTrack._id,
    //       closeModel,
    //       tableFilter,
    //     }),
    //   );
    // }
  };

  return (
    <Model
      closeModel={closeModel}
      headerTitle="Tracking Order"
      modalId="trackDeliveryBoy"
      modalClass="modal-lg"
      //   submitBtnText="Cancel"
    >
      {statusButtonText && (
        <div className="printview-order-inner m-b-md">
          <button
            type="button"
            style={{ width: '150px' }}
            id="printdel"
            className="btn printleft btn-primary"
            onClick={() => handleStatusChanges()}
          >
            {statusButtonText}
          </button>
        </div>
      )}
      <div style={{ marginTop: '0px' }}>
        <div className="form_header">
          <h1>Order Information:</h1>
        </div>
        <div className="row ">
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
            <div className="detail-title-a">
              <p>
                <span> Order Number:</span>
                {orderData.orderNumber}
              </p>
              <p>
                <span>Order Date: </span>
                {moment(orderData.createdAt).format('DD/MM/YYYY')}
              </p>
            </div>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
            <div className="detail-title-a">
              <p>
                <span>Order Time:</span>
                {moment(orderData.createdAt).format('hh:mm A')}
              </p>
              <p>
                <span>Order Total: </span>
                {orderData.total}
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className="form_header">
        <h1>Order Tracking</h1>
      </div>
      {indexTrackingOrderStatus !== 7 && (
        <div>{`Status will be update in ${time} seconds`}</div>
      )}
      <div className="kyc-top-bar clearfix">
        <ul>
          <li className={indexTrackingOrderStatus >= 0 ? 'current' : ''}>
            <div className="box">
              <div className="icon ic-3">
                <i className="fa fa-download" aria-hidden="true" />
              </div>
            </div>
            <p>Pending</p>
          </li>
          <li className={indexTrackingOrderStatus >= 1 ? 'current' : ''}>
            <div className="box">
              <div className="icon ic-3">
                <i className="fa fa-check" aria-hidden="true" />
              </div>
            </div>
            <p>Accepted</p>
          </li>
          <li className={indexTrackingOrderStatus >= 2 ? 'current' : ''}>
            <div className="box">
              <div className="icon ic-3">
                <i className="fa fa-check" aria-hidden="true" />
              </div>
            </div>
            <p>Preparing</p>
          </li>
          <li className={indexTrackingOrderStatus >= 3 ? 'current' : ''}>
            <div className="box">
              <div className="icon ic-3">
                <i className="fa fa-motorcycle" aria-hidden="true" />
              </div>
            </div>
            <p>Ready for Pickup</p>
          </li>
          {!orderData.isSelfPickUp && (
            <li className={indexTrackingOrderStatus >= 4 ? 'current' : ''}>
              <div className="box">
                <div className="icon ic-3">
                  <i className="fa fa-motorcycle" aria-hidden="true" />
                </div>
              </div>
              <p>On the way to Pick up</p>
            </li>
          )}
          {!orderData.isSelfPickUp && (
            <li className={indexTrackingOrderStatus >= 5 ? 'current' : ''}>
              <div className="box">
                <div className="icon ic-3">
                  <i className="fa fa-check" aria-hidden="true" />
                </div>
              </div>
              <p>Picked up</p>
            </li>
          )}
          {!orderData.isSelfPickUp && (
            <li className={indexTrackingOrderStatus >= 6 ? 'current' : ''}>
              <div className="box">
                <div className="icon ic-3">
                  <i className="fa fa-motorcycle" aria-hidden="true" />
                </div>
              </div>
              <p>On the way to Delivey</p>
            </li>
          )}
          <li className={indexTrackingOrderStatus >= 7 ? 'current' : ''}>
            <div className="box">
              <div className="icon ic-3">
                <i className="fa fa-check" aria-hidden="true" />
              </div>
            </div>
            <p>Delivered</p>
          </li>
        </ul>
      </div>
      <div className="track_ship_flow" />
    </Model>
  );
};

export default TrackOrderModal;
