/* eslint-disable react/prop-types */
import React, { useState, useRef } from 'react';
import moment from 'moment';
import { useReactToPrint } from 'react-to-print';
import Model from '../../../components/UI/Model/Model';

// eslint-disable-next-line no-unused-vars
const ViewOrderModal = props => {
  const { orderData } = props;
  const [cookingInstructions] = useState(orderData.cookingInstructionsByAdmin);
  const restaurantAddress = [];
  const customerAddress = [];
  const printComponentRef = useRef();

  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };

  const genrateAddress = (address, addressArray) => {
    if (address) {
      const {
        apartment,
        area,
        city,
        houseNo,
        landmark,
        pincode,
        street,
      } = address;
      if (houseNo) {
        addressArray.push(houseNo);
      }
      if (apartment) {
        addressArray.push(apartment);
      }
      if (landmark) {
        addressArray.push(landmark);
      }
      if (area) {
        addressArray.push(area);
      }
      if (city) {
        addressArray.push(city);
      }
      if (street) {
        addressArray.push(street);
      }
      if (pincode) {
        addressArray.push(pincode);
      }
    }
  };

  genrateAddress(orderData.restaurantAddress, restaurantAddress);
  genrateAddress(orderData.orderDeliverAddress, customerAddress);

  const handlePrint = useReactToPrint({
    content: () => printComponentRef.current,
    pageStyle: 'margin: 40px',
    documentTitle: 'Invoice',
  });

  return (
    <Model
      closeModel={closeModel}
      headerTitle="Order Details"
      modalId="printpreview"
      modalClass="modal-small"
      cancelBtnText="Add"
      submitBtnText="Print"
      submitBtnHandler={handlePrint}
    >
      {/* <button
        type="button"
        style={{ width: '150px' }}
        id="printdel"
        className="btn printleft btn-primary"
      >
        <i className="fa fa-print" style={{ marginRight: '10px' }} />
        Print
      </button> */}
      <div ref={printComponentRef}>
        <div className="printview-order" id="printableArea">
          <div className="printview-order-inner">
            <table className="table" style={{ marginBottom: 0 }}>
              <tbody>
                <tr>
                  <td style={{ paddingLeft: 0 }}>
                    <p>Invoice Number:</p>
                  </td>
                  <td colSpan={3} style={{ textAlign: 'left' }}>
                    <b>
                      <span>{orderData.orderNumber}</span>
                    </b>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div className="printview-order" id="printableArea">
          <div className="printview-order-inner">
            <h3 className="sub_title">Customer Details</h3>

            <table className="table" style={{ marginBottom: 2 }}>
              <tbody>
                <tr>
                  <td>
                    <p>Full Name :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.userData.name}</p>
                    </b>
                  </td>
                </tr>
                {/* <tr>
                  <td>
                    <p>Mobile :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.userData.phone}</p>
                    </b>
                  </td>
                </tr> */}
                <tr>
                  <td>
                    <p>Address :</p>
                  </td>
                  <td>
                    <b>
                      <p>{customerAddress.join(', ')}</p>
                    </b>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="printview-order-inner">
            <h3 className="sub_title">Vendor Details</h3>

            <table className="table">
              <tbody>
                <tr>
                  <td>
                    <p>Full Name :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.restaurantName}</p>
                    </b>
                  </td>
                </tr>
                <tr>
                  <td>
                    <p>Mobile :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.retsaurantContact}</p>
                    </b>
                  </td>
                </tr>
                <tr>
                  <td>
                    <p>Address :</p>
                  </td>
                  <td>
                    <b>
                      <p>{restaurantAddress.join(', ')}</p>
                    </b>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="printview-order-inner order_details">
            <h3 className="sub_title">Order Details</h3>

            <table className="table ">
              <tbody>
                {/* <tr>
                  <td>
                    <p>Order ID :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.orderNumber}</p>
                    </b>
                  </td>
                </tr> */}
                <tr>
                  <td>
                    <p>Order Date :</p>
                  </td>
                  <td>
                    <b>
                      <p>
                        {moment(orderData.createdAt).format(
                          'DD/MM/YYYY hh:mm A ',
                        )}
                      </p>
                    </b>
                  </td>
                </tr>
                <tr>
                  <td>
                    <p>Order Type :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.isSelfPickUp ? 'PickUp' : 'Delivery'}</p>
                    </b>
                  </td>
                </tr>
                <tr>
                  <td>
                    <p>Payment Option :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.isCODOrder ? 'COD' : 'Online'}</p>
                    </b>
                  </td>
                </tr>
                <tr>
                  <td>
                    <p>Payment Status :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.paymentStatus}</p>
                    </b>
                  </td>
                </tr>
                <tr>
                  <td>
                    <p>Customer Comments :</p>
                  </td>
                  <td>
                    <b>
                      <p>{orderData.cookingInstructions || '-'}</p>
                    </b>
                  </td>
                </tr>
                <tr>
                  <td>
                    <p>Admin Comments :</p>
                  </td>
                  <td>
                    <b>
                      <p>{cookingInstructions || '-'}</p>
                    </b>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          {/* overall total table  */}
          <div className="overallTable table-responsive ">
            <table
              className="table table-striped table-bordered"
              id="order-table"
            >
              <thead>
                <tr>
                  <th>S.No.</th>
                  <th>Item Name</th>
                  <th>Qty</th>
                  <th>Price (INR)</th>
                  <th>Variety - Price (INR)</th>
                </tr>
              </thead>
              <tbody>
                {orderData.dishList.map((item, index) => (
                  <tr key={item._id}>
                    <td className="s_no">{index + 1}</td>
                    <td>{item.name}</td>
                    <td>{item.quantity}</td>
                    <td>{item.price.toFixed(2)}</td>
                    <td className="ing">
                      <ul className="tbl_view">
                        <li>
                          <span className="td lbl">
                            {`INR ${(item.quantity * item.price).toFixed(2)}`}
                          </span>
                        </li>
                      </ul>
                    </td>
                  </tr>
                ))}
                <tr className="total_box">
                  <td colSpan={4} />
                  <td>
                    <ul className="tbl_view">
                      <li>
                        <span className="td">Subtotal</span>
                        <span className="td lbl">{`INR ${orderData.total}`}</span>
                      </li>
                      {/* <li>
                        <span className="td">Offer Value</span>
                        <span className="td lbl">{`INR ${orderData.offerValue}`}</span>
                      </li> */}
                      <li>
                        <span className="td">Discount </span>
                        <span className="td lbl">{`INR ${orderData.discount}`}</span>
                      </li>
                      <li>
                        <span className="td">Rider Tip</span>
                        <span className="td lbl">{`INR ${orderData.riderTip}`}</span>
                      </li>
                      <li>
                        <span className="td">{`Tax (${orderData.GST}%)`}</span>
                        <span className="td lbl">{`INR ${orderData.taxCharge}`}</span>
                      </li>
                      <li>
                        <span className="td">Delivery Fee</span>
                        <span className="td lbl">{`INR ${orderData.deliveryCharge}`}</span>
                      </li>
                      <li>
                        <span className="td">Packaging Fee</span>
                        <span className="td lbl">{`INR ${orderData.packagingCharge}`}</span>
                      </li>
                      <li>
                        <span className="td">Total</span>
                        <span className="td lbl">{`INR ${orderData.grandTotal} `}</span>
                      </li>
                    </ul>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      {/* <div className="modal-footer">
        <button
          type="button"
          className="btn grey"
          data-dismiss="modal"
          onClick={() => closeModel()}
        >
          Close
        </button>
      </div> */}
    </Model>
  );
};

export default ViewOrderModal;
